.menu,
.menuItem {
  background-color: rgba(254, 254, 254, 0.3); }

.menu {
  border-bottom: 1px solid rgba(178, 178, 178, 0.3);
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }

.menuItem {
  border-bottom: 1px solid rgba(178, 178, 178, 0.3);
  height: 50px;
  display: inline-block;
  padding: 0 50px;
  position: relative;
  transition: background-color 0.2s, box-shadow 0.5s;
  line-height: 50px;
  border-right: 1px solid rgba(178, 178, 178, 0.3);
  box-shadow: 0 0 0 transparent inset;
  font-size: 16px; }
  .menuItem, .menuItem:hover, .menuItem:active, .menuItem:visited, .menuItem:focus {
    color: #646464; }
  .menuItem:hover, .menuItem.active {
    box-shadow: 0 0 15px rgba(221, 221, 221, 0.75) inset;
    background-color: transparent;
    cursor: pointer; }
  .menuItem:first-child {
    border-left: 1px solid rgba(178, 178, 178, 0.3); }
